import { useState } from 'react';
import { motion } from 'framer-motion';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import './ContactSection.css';
import { useScrollAnimation } from '../hooks/useScrollAnimation';

//  
import callIcon from '../images/icons/call.png';
import priceIcon from '../images/icons/price-tag.png';
import documentsIcon from '../images/icons/paper-documents.png';
import publicRelationIcon from '../images/icons/public-relation.png';

//  
const schema = yup.object({
  name: yup
    .string()
    .required("' '")
    .min(2, "'    2 ")
    .matches(/^[--a-zA-Z\s]+$/, "'    "),
  phone: yup
    .string()
    .required(" '")
    .matches(/^\+?3?8?(0\d{9})$/, "   "),
  email: yup
    .string()
    .required("Email '")
    .email("  email"),
  carModel: yup.string(),
  message: yup.string()
}).required();

const ContactSection = () => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  //   
  const [titleRef, titleVisible] = useScrollAnimation(0.1);
  const [infoRef, infoVisible] = useScrollAnimation(0.1);
  const [featuresRef, featuresVisible] = useScrollAnimation(0.1);
  const [formRef, formVisible] = useScrollAnimation(0.1);

  // React Hook Form
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset
  } = useForm({
    resolver: yupResolver(schema),
    mode: 'onBlur'
  });

  const onSubmit = async (data) => {
    setIsSubmitting(true);
    try {
      //   
      await new Promise(resolve => setTimeout(resolve, 2000));
      console.log('Form submitted:', data);
      setSubmitSuccess(true);
      reset();
      setTimeout(() => setSubmitSuccess(false), 5000);
    } catch (error) {
      console.error('Error submitting form:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <section id="contact" className="contact-section">
      <div className="container">
        <div className="contact-content">
          <div className="contact-info">
            <div 
              ref={titleRef}
              className={`contact-title ${titleVisible ? 'animate-in' : ''}`}
            >
              <h2 className="section-title">
                 <span className="highlight">?</span>
              </h2>
            </div>
            
            <div 
              ref={infoRef}
              className={`contact-info-content ${infoVisible ? 'animate-in' : ''}`}
            >
              <p className="contact-description">
                     '    15 
                     .
              </p>
            </div>
            
            <motion.div 
              ref={featuresRef}
              className={`contact-features ${featuresVisible ? 'animate-in' : ''}`}
              initial={{ opacity: 0, y: 30 }}
              animate={featuresVisible ? { opacity: 1, y: 0 } : {}}
              transition={{ duration: 0.6 }}
            >
              <motion.div 
                className="contact-feature"
                initial={{ opacity: 0, x: -30 }}
                animate={featuresVisible ? { opacity: 1, x: 0 } : {}}
                transition={{ delay: 0.1, duration: 0.5 }}
              >
                <div className="feature-icon">
                  <img src={callIcon} alt=" " />
                </div>
                <div className="feature-text">
                  <h4> </h4>
                  <p>    </p>
                </div>
              </motion.div>
              
              <motion.div 
                className="contact-feature"
                initial={{ opacity: 0, x: -30 }}
                animate={featuresVisible ? { opacity: 1, x: 0 } : {}}
                transition={{ delay: 0.2, duration: 0.5 }}
              >
                <div className="feature-icon">
                  <img src={priceIcon} alt=" " />
                </div>
                <div className="feature-text">
                  <h4> </h4>
                  <p>     </p>
                </div>
              </motion.div>
              
              <motion.div 
                className="contact-feature"
                initial={{ opacity: 0, x: -30 }}
                animate={featuresVisible ? { opacity: 1, x: 0 } : {}}
                transition={{ delay: 0.3, duration: 0.5 }}
              >
                <div className="feature-icon">
                  <img src={documentsIcon} alt="" />
                </div>
                <div className="feature-text">
                  <h4> </h4>
                  <p>     </p>
                </div>
              </motion.div>
              
              <motion.div 
                className="contact-feature"
                initial={{ opacity: 0, x: -30 }}
                animate={featuresVisible ? { opacity: 1, x: 0 } : {}}
                transition={{ delay: 0.4, duration: 0.5 }}
              >
                <div className="feature-icon">
                  <img src={publicRelationIcon} alt="" />
                </div>
                <div className="feature-text">
                  <h4> </h4>
                  <p>    </p>
                </div>
              </motion.div>
            </motion.div>
          </div>
          
          <motion.div 
            ref={formRef}
            className={`contact-form-wrapper ${formVisible ? 'animate-in' : ''}`}
            initial={{ opacity: 0, scale: 0.95 }}
            animate={formVisible ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: 0.6 }}
          >
            <form className="contact-form" onSubmit={handleSubmit(onSubmit)}>
              <h3> </h3>
              
              {submitSuccess && (
                <motion.div 
                  className="success-message"
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0 }}
                >
                     !  '    .
                </motion.div>
              )}
              
              <div className="form-group">
                <input
                  type="text"
                  placeholder=" '"
                  {...register('name')}
                  className={errors.name ? 'error' : ''}
                />
                {errors.name && (
                  <motion.span 
                    className="error-message"
                    initial={{ opacity: 0, y: -5 }}
                    animate={{ opacity: 1, y: 0 }}
                  >
                    {errors.name.message}
                  </motion.span>
                )}
              </div>
              
              <div className="form-group">
                <input
                  type="tel"
                  placeholder=" "
                  {...register('phone')}
                  className={errors.phone ? 'error' : ''}
                />
                {errors.phone && (
                  <motion.span 
                    className="error-message"
                    initial={{ opacity: 0, y: -5 }}
                    animate={{ opacity: 1, y: 0 }}
                  >
                    {errors.phone.message}
                  </motion.span>
                )}
              </div>
              
              <div className="form-group">
                <input
                  type="email"
                  placeholder="Email"
                  {...register('email')}
                  className={errors.email ? 'error' : ''}
                />
                {errors.email && (
                  <motion.span 
                    className="error-message"
                    initial={{ opacity: 0, y: -5 }}
                    animate={{ opacity: 1, y: 0 }}
                  >
                    {errors.email.message}
                  </motion.span>
                )}
              </div>
              
              <div className="form-group">
                <input
                  type="text"
                  placeholder="  (')"
                  {...register('carModel')}
                />
              </div>
              
              <div className="form-group">
                <textarea
                  placeholder=" "
                  {...register('message')}
                  rows="4"
                ></textarea>
              </div>
              
              <motion.button 
                type="submit" 
                className="submit-button"
                disabled={isSubmitting}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                {isSubmitting ? (
                  <span className="loading-spinner">...</span>
                ) : (
                  ' '
                )}
              </motion.button>
            </form>
          </motion.div>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;